<template>
  <div class="collection">
    <div class="rg-top-top">
      <span class="content-top-text-blue"></span>
      <strong style="margin-left: 2px">{{company[0].shenbao_month}} 社保表报送</strong>

      <div class="rg-content">
        <div class="rg-content-left">
          <span style="display: inline-block;width: 68px;height: 40px">
            <span>申报类型</span>
            <span class="text-orange">正常申报</span>
          </span>
          <span
            style="display: inline-block;margin-bottom: -5px; width: 1px;height: 40px;border-right: solid 1px #E3E4E8"
          ></span>
          <span style="display: inline-block; width: 68px;height: 40px">
            <span>申报状态</span>
            <span class="text-orange">{{ info }}</span>
          </span>
        </div>
        <div class="rg-content-right">
          <el-dropdown>
            <el-button type="text" size="small" class="right-btn">
              导出申报表
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native=" nullClick">标准样式</el-dropdown-item>
              <el-dropdown-item @click.native=" nullClick">大厅申报样式</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <el-button type="primary" size="small" @click="qi = true">发送申报</el-button>
        </div>
      </div>
    </div>

    <div style="border: solid 1px #e0e0e0;margin: 0 10px; width:calc(100% - 22px)">
      <div id="con_in" class="con-div" style="overflow-y: auto; display: block;">
        <el-table :data="extraTableData" border>
          <!-- <el-table-column type="selection"  width="55"></el-table-column> -->
          <el-table-column prop="name" label="报表名称"></el-table-column>
          <el-table-column prop="personNumber" label="纳税人数"></el-table-column>
          <el-table-column prop="income" label="本期收入"></el-table-column>
          <el-table-column prop="incomeAll" label="累计收入"></el-table-column>
          <el-table-column prop="taxPay" label="应扣缴税额"></el-table-column>
          <el-table-column prop="taxPayed" label="已缴税额"></el-table-column>
          <el-table-column prop="taxReturn" label="应补(退)税额"></el-table-column>
          <el-table-column prop="isCanSubmit" label="是否可申报"></el-table-column>
        </el-table>
      </div>
    </div>

    <el-dialog
      class="in-table-system"
      title="境内人员信息"
      :visible.sync="qi"
      width="461px"
      height="320px"
      top="25vh"
    >
      <div
        class="layui-layer-title"
        style="color: rgb(51, 51, 51); background-color: rgb(255, 255, 255); border: 1px solid rgb(142, 142, 142); "
        move="ok"
      >申报密码
      </div>

      <a
        class="layui-layer-ico layui-layer-close layui-layer-close1"
        @click="qi = false"
        href="javascript:;"
        style="width:15px;height:15px;position:absolute;top:15px;right:15px;background-position: 0 -40px;"
      ></a>
      <div id="submit_content" class="layui-layer-wrap" style="display: block;">
        <!--企业名称-->
        <div class="name-div">{{company[0].name}}</div>
        <!--输入申报密码-->
        <input type="password" class="input-text size-S" v-model="mima" id="submit_pwd">
        <!--密码提示-->
        <div class="model-div">【模拟系统默认初始密码为：000000】</div>
        <!--按钮-->
        <div class="submit-btn-div">
          <!--修改密码-->
          <div id="pwd_change" @click="$message.warning('教学系统暂不支持修改密码')"></div>
          <!--确认-->
          <div id="pwd_confirm" @click="shenbaoConfirm"></div>
          <!--取消-->
          <div id="pwd_cancel" @click="qi=false"></div>
        </div>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      qi: false,
      mima: '000000',
      info: '待申报'
    }
  },
  created () {
    this.$emit('router', this.$router.history.current.name)
    this.$emit('pathNumber', 4)
  },
  computed: {
    ...mapGetters(['company']),
    extraTableData () { // 表格
      let extraTableData = []
      var normal = this.$store.state.form.normalForm
      var year = this.$store.state.form.yearForm
      var labor = this.$store.state.form.laborForm
      let name = ['综合所得预扣预缴表', '正常工资薪金', '一次性奖金收入', '劳务报酬所得']
      let tableName = ['all', normal, year, labor]
      for (let i in name) {
        extraTableData[i] = {}
        extraTableData[i].name = name[i]
        switch (i) {
          case '0':
            extraTableData[i].personNumber = normal.length + year.length + labor.length
            extraTableData[i].income = (parseFloat(this.add(tableName[1], 'income')) + parseFloat(this.add(tableName[2], 'income')) + parseFloat(this.add(tableName[3], 'income'))).toFixed(2)
            extraTableData[i].incomeAll = (parseFloat(this.add(tableName[1], 'incomeAll')) + parseFloat(this.add(tableName[2], 'incomeAll')) + parseFloat(this.add(tableName[3], 'incomeAll'))).toFixed(2)
            extraTableData[i].taxPay = (this.add3(this.add(tableName[1], 'yingkoujiaoshuie'), this.add(tableName[2], 'yingkoujiaoshuie'), this.add(tableName[3], 'yingkoujiaoshuie'))).toFixed(2)
            extraTableData[i].taxPayed = (this.add3(this.add2(tableName[1], 'otherAll', 'yikoujiaoshuie'), this.add2(tableName[2], 'otherAll', 'yikoujiaoshuie'), this.add2(tableName[3], 'otherAll', 'yikoujiaoshuie'))).toFixed(2)
            extraTableData[i].taxReturn = (this.add3(this.add(tableName[1], 'tuibu'), this.add(tableName[2], 'tuibu'), this.add(tableName[3], 'tuibu'))).toFixed(2)
            break
          default:
            extraTableData[i].personNumber = tableName[i].length
            extraTableData[i].income = this.add(tableName[i], 'income')
            extraTableData[i].incomeAll = this.add(tableName[i], 'incomeAll')
            extraTableData[i].taxPay = this.add(tableName[i], 'yingkoujiaoshuie')
            extraTableData[i].taxPayed = this.add2(tableName[i], 'otherAll', 'yikoujiaoshuie')
            extraTableData[i].taxReturn = this.add(tableName[i], 'tuibu')
            break
        }
        extraTableData[i].isCanSubmit = '是'
      }
      console.log(extraTableData)
      return extraTableData
    }
  },
  methods: {
    showNull () {
      this.$alert('本系统为教学系统，目前只提供常用操作功能噢', '信息', {
        confirmButtonText: '确定'
      })
    },
    nullClick () {
      this.showNull()
    },

    add (arr, key) { // 用于数组加数的
      let num = 0
      for (let i in arr) {
        num += parseFloat(arr[i][key])
      }
      return num.toFixed(2)
    },
    add2 (arr, key1, key2) { // 双层的对象数组加数
      let num = 0
      for (let i in arr) {
        num += parseFloat(arr[i][key1][key2])
      }
      return num.toFixed(2)
    },
    add3 (a, b, c) { // 把三个数加起来
      let num = 0
      num = parseFloat(a) + parseFloat(b) + parseFloat(c)
      return num
    },
    shenbaoConfirm () {
      if (this.extraTableData[0].personNumber === 0) {
        this.$message({
          type: 'error',
          message: '请选择要报送的信息!'
        })
      } else {
        if (this.mima !== '000000') {
          this.$message.warning('密码错误')
          return false
        }
        this.$store.commit('upShenbaoForm', this.extraTableData[0])
        this.info = '已申报'
        // 待申报修改
        this.$alert('申报成功，赶紧去缴款吧', '信息', {
          confirmButtonText: '确定'
        })
        this.qi = false
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  div {
    font-size: 14px;
  }

  h1,
  h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  .el-table {
    font-size: 14px;
  }

  .rg-content {
    line-height: 18px;
    margin: 0 10px 0 10px;

    display: flex;
    justify-content: space-between;
  }

  .rg-top-top {
    border: 1px solid #e0e0e0;
    line-height: 40px;
    box-sizing: border-box;
    margin: 10px;
    padding: 10px;
  }

  .top-div {
    flex: 1;
    background-color: #f4f6f9;
    color: #666666;
    text-align: center;
    border-radius: 3px;
    cursor: pointer;
  }

  .text-orange {
    color: #f7ad7c;
    line-height: 20px;
  }

  .el-dropdown {
    border: 1px solid rgb(90, 152, 222);
    border-radius: 4px;
    padding: 0 5px;
  }

  .layui-layer-title {
    padding: 0 80px 0 20px;
    height: 42px;
    line-height: 42px;
    border-bottom: 1px solid #eee;
    font-size: 14px;
    color: #333;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    background-color: #F8F8F8;
    border-radius: 2px 2px 0 0;
  }

  .layui-layer-ico {
    background: url(../../../../static/img/iconClose.png) no-repeat;
    background-position: 0 -40px;
    cursor: pointer;
  }

  #submit_content {
    float: left;
    width: 461px;
    height: 274px;
    background: url(../../../../static/img/submit-pwd.png) no-repeat center;
    display: none;
  }

  .name-div {
    float: left;
    width: 300px;
    height: 30px;
    line-height: 30px;
    font-size: 14px;
    margin: 16px 0px 0px 145px;
  }

  #submit_pwd {
    float: left;
    width: 198px;
    margin: 15px 0px 0px 147px;
  }

  .model-div {
    float: left;
    width: 300px;
    height: 30px;
    margin: 100px 0px 0px 95px;
    font-size: 14px;
    color: #ff0000;
  }

  .submit-btn-div {
    float: left;
    width: 100%;
    height: 38px;
    margin-top: 13px;
  }

  #pwd_change {
    float: left;
    width: 80px;
    height: 38px;
    margin-left: 177px;
    cursor: pointer;
  }

  #pwd_confirm {
    float: left;
    width: 80px;
    height: 38px;
    margin-left: 15px;
    cursor: pointer;
  }

  #pwd_cancel {
    float: left;
    width: 80px;
    height: 38px;
    margin-left: 15px;
    cursor: pointer;
  }

  #pwd_content {
    float: left;
    width: 461px;
    height: 274px;
    background: url(/static/img/submit-pwd1.png) no-repeat center;
    display: none;
  }

  .pwd-change-input {
    float: left;
    width: 170px;
  }

  .pwd-btn-div {
    float: left;
    width: 100%;
    height: 38px;
    margin-top: 86px;
  }

  #change_confirm {
    float: left;
    width: 80px;
    height: 38px;
    margin-left: 272px;
    cursor: pointer;
  }

  #change_cancel {
    float: left;
    width: 80px;
    height: 38px;
    margin-left: 15px;
    cursor: pointer;
  }

  #feedBack_content {
    float: left;
    width: 500px;
    height: 315px;
    background: url("/static/img/submit-info.png") no-repeat center;
    display: none;
  }

  #time1 {
    width: 300px;
    height: 20px;
    line-height: 20px;
    margin: 108px 0px 0px 70px
  }

  #feedBack_cancel {
    float: left;
    width: 65px;
    height: 27px;
    margin: 150px 0px 0px 428px;
    cursor: pointer;
  }

  #feedBack2_content {
    float: left;
    width: 500px;
    height: 315px;
    background: url("/static/img/submit-info.png") no-repeat center;
    display: none;
  }

  #time2 {
    width: 300px;
    height: 20px;
    line-height: 20px;
    margin: 108px 0px 0px 70px
  }

  #feedBack2_cancel {
    float: left;
    width: 65px;
    height: 27px;
    margin: 150px 0px 0px 428px;
    cursor: pointer;
  }

  .content-top-text-blue {
    float: left;
    width: 4px;
    height: 20px;
    background-color: #4285F4;
    border-radius: 5px;
    margin: 10px 10px 10px 0
  }

</style>



